<template>
  <div v-loading.fullscreen.lock="loading" class="container" element-loading-background="rgba(0, 0, 0, 0.8)">
    <el-row class="title MT-30" :gutter="30">
      <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
        <div class="title-item MB-30 flex-row-center">
          <img src="~@/assets/images/presale/commission.png" class="title-item__pic title-item__commission MR-30">
          <div class="text-left">
            <div class="text-20 text-white MB-15">Personal Commission</div>
            <div class="text-26 text-commission text-bold">SDOGE：{{ personal || 0 }}</div>
            <div class="text-26 text-commission text-bold">USDT：{{ personalUSDT || '0.00' }}</div>
          </div>
        </div>
      </el-col>
      <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
        <div class="title-item MB-30 flex-row-center">
          <img src="~@/assets/images/presale/purchase.png" class="title-item__pic MR-30">
          <div class="text-left">
            <div class="text-20 text-white MB-15">Total purchase</div>
            <div class="text-26 text-yellow text-bold">SDOGE：{{ total || 0 }}</div>
            <div class="text-26 text-yellow text-bold">USDT：{{ totalUSDT || 0 }}</div>
          </div>
        </div>
      </el-col>
    </el-row>

    <div class="tab PB-40">
      <div class="tab-list text-26 text-bold flex-row-center text-white" :class="{ two: tabIndex == 1 }">
        <div class="tab-list__item pointer" :class="{ 'text-yellow': tabIndex == 0 }" @click="toggleTab(0)">Invitation</div>
        <div class="tab-list__item pointer" :class="{ 'text-yellow': tabIndex == 1 }" @click="toggleTab(1)">Invitation list</div>
      </div>
      <div v-show="tabIndex == 0" class="tab-content MT-40">
        <div class="tab-content__title flex-row-start">
          <div class="tab-list__one PL-10 PR-10">Invitation address</div>
          <div class="tab-list__two">SDOGE</div>
          <div class="tab-list__three">USDT</div>
        </div>
        <div class="tab-content__list text-white text-16">
          <div v-for="item in tableList" :key="item" class="tab-content__item flex-row-start">
            <div class="tab-list__one break-word PL-10 PR-10 flex-row-center">
              <a class="text-white MR-10" :href="'https://bscscan.com/token/0x9e551b430b7ab145c96c6ede72a34ec2a90d4eca?a=' + item.address" target="_blank">
                {{ item.address }}
              </a>
              <el-icon class="pointer" @click="copyHandler(item.address)"><document-copy /></el-icon>
            </div>
            <div class="tab-list__two">{{ item.profit }}</div>
            <div class="tab-list__three">{{ item.usdt }}</div>
          </div>
          <el-empty v-if="!tableList || tableList.length <= 0" :image-size="200" />
        </div>
        <el-pagination
          v-model:current-page="currentPage"
          class="MT-10"
          background
          layout="prev, pager, next"
          :total="totalNumber"
          :pager-count="10"
          :page-size="limit"
          @current-change="pageChange"
        />
      </div>
      <div v-show="tabIndex == 1" class="tab-content MT-40">
        <div class="tab-content__title flex-row-start">
          <div class="tab-list__one PL-10 PR-10">Invitation address</div>
          <div class="tab-list__two">Profit</div>
        </div>
        <div class="tab-content__list text-white text-16">
          <div v-for="(item, index) in invitationList" :key="index" class="tab-content__item flex-row-start">
            <div class="tab-list__one break-word PL-10 PR-10">{{ item.address }}</div>
            <div class="tab-list__two">
              <span v-if="item.status == 0" class="text-16 text-yellow">Testing</span>
              <span v-if="item.status == -1" class="text-16 text-red">Detection failed</span>
              <el-icon v-if="item.status == 1" color="#52C41A" class="ML-10"><icon-select /></el-icon>
            </div>
          </div>
          <div v-loading="addLoading" element-loading-background="rgba(0, 0, 0, 0.8)">
            <div class="tab-user PT-30 PB-30">
              <div class="text-16 text-bold text-white text-left MB-20">BEP-20 Wallet</div>
              <div class="tab-user__text">
                <textarea v-model.trim="userInfo" />
              </div>
              <div class="flex-row-between text-16 MT-20">
                <div class="tab-user__btn tab-user__add pointer" @click="addUser">Add Wallet</div>
                <div class="tab-user__btn tab-user__invitation pointer" @click="invitationHandler">invitation</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Select, DocumentCopy } from '@element-plus/icons'
import { ElNotification } from 'element-plus'
import { utils } from 'ethers'
import copy from '@/libs/copy.js'

export default {
  components: {
    IconSelect: Select,
    DocumentCopy
  },
  data() {
    return {
      // 当前tab
      tabIndex: 0,
      // 已邀请列表
      tableList: [],
      // 初始化loading
      loading: false,
      // 页面数据
      purchase: {},
      // 当前邀请列表
      invitationList: [],
      userInfo: '',
      addLoading: false,
      // 从哪里开始
      from: 0,
      // 每页多少条
      limit: 10,
      // 个人佣金SDOGE
      personal: 0,
      // 个人佣金USDT
      personalUSDT: 0,
      // 总佣金SDOGE
      total: 0,
      // 总佣金USDT
      totalUSDT: 0,
      // 总条数
      totalNumber: 0,
      // 当前页数
      currentPage: 1,
      // 转换美元实例
      USDT: null
    }
  },
  mounted() {
    // this.getInvitation()
    setTimeout(() => {
      this.$wallet.initNetwork()
      this.$wallet.on('accountChange', this.accountChange)
    }, 500)
  },
  methods: {
    copyHandler(content) {
      copy(content)
      ElNotification({
        type: 'success',
        title: 'Tips',
        message: 'Copy succeeded'
      })
    },
    // 页数改变
    pageChange() {
      this.getInvitation()
    },
    // 账号变化
    accountChange() {
      this.total = 0
      this.totalUSDT = '0.00'
      this.personal = 0
      this.personalUSDT = '0.00'
      this.totalNumber = 0
      this.currentPage = 1
      this.tableList = []
      this.loading = false
      this.purchase = {}
      this.invitationList = []
      this.userInfo = ''
      if (this.$wallet.accounts.length > 0) {
        this.getInvitation()
      }
    },
    // 添加地址
    async addUser() {
      if (!this.userInfo) {
        ElNotification({
          type: 'error',
          title: 'Tips',
          message: 'The list is empty'
        })
        return
      }
      const userList = this.userInfo.split('\n')
      this.userInfo = ''
      try {
        const list = userList.map((item) => {
          const res = {}
          res.status = 0
          res.address = item
          return res
        })
        this.invitationList = [...this.invitationList, ...list]
        this.checkUser()
      } catch (error) {
        ElNotification({
          title: 'Error',
          message: error.data ? error.data.message || error.message : error.message,
          type: 'error'
        })
      }
    },
    // 检测地址
    async checkUser() {
      const network = await this.$wallet.initNetwork()
      const { scoutDogeABI } = network
      this.invitationList.forEach((item) => {
        scoutDogeABI.estimateGas
          .manyInvitePlayers([item.address])
          .then((data) => {
            item.status = 1
          })
          .catch(() => {
            item.status = -1
          })
      })
    },
    // 邀请当前输入列表
    async invitationHandler() {
      if (this.invitationList.length <= 0) {
        ElNotification({
          title: 'Tips',
          message: 'The current invitation list is empty',
          type: 'error'
        })
        return
      }
      let isEnd = true
      const list = []
      for (let index = 0; index < this.invitationList.length; index++) {
        const item = this.invitationList[index]
        if (item.status === 0) {
          isEnd = false
          break
        }
        if (item.status === 1) {
          list.push(item.address)
        }
      }
      if (list.length <= 0) {
        ElNotification({
          title: 'Tips',
          message: 'The current invitation list is empty',
          type: 'error'
        })
        return
      }
      // 如果当前检测未结束，则停止
      if (!isEnd) {
        ElNotification({
          title: 'Tips',
          message: 'Testing, please wait...',
          type: 'error'
        })
        return
      }
      this.addLoading = true
      try {
        const network = await this.$wallet.initNetwork()
        const { scoutDogeABI } = network
        const tx = await scoutDogeABI.manyInvitePlayers(list)
        await tx.wait()
        this.addLoading = false
        this.tabIndex = 0
        this.currentPage = 1
        this.invitationList = []
        ElNotification({
          title: 'Tips',
          message: 'Invitation succeeded',
          type: 'success'
        })
        this.getInvitation()
      } catch (error) {
        ElNotification({
          title: 'Error',
          message: error.data ? error.data.message || error.message : error.message,
          type: 'error'
        })
        this.addLoading = false
      }
    },
    // 获取邀请列表
    async getInvitation() {
      this.loading = true
      try {
        const network = await this.$wallet.initNetwork()
        const { scoutDogeABI, accounts } = network
        if (accounts.length <= 0) {
          this.loading = false
          return
        }
        const tx = await scoutDogeABI.getSubordinateSize(accounts[0])
        await this.getUSDT()

        const number = tx.toNumber()
        this.totalNumber = number
        if (number > 0) {
          this.getAll(number)
          const offset = (this.currentPage - 1) * this.limit
          let limit = this.limit
          // 如果当前每页条数+起始数大于总条数，则limit要修正
          if (this.limit + offset > number) {
            limit = number - offset
          }
          const res = await scoutDogeABI.getSubordinate(offset, limit)
          this.tableList = res.map((item) => {
            const data = {}
            data.address = item
            data.profit = 'wait...'
            return data
          })
          this.searchAddressProfit()
        }
        this.loading = false
      } catch (error) {
        console.log(error)
        this.loading = false
        ElNotification({
          title: 'Error',
          message: error.data ? error.data.message || error.message : error.message,
          type: 'error'
        })
      }
    },
    // 获取所有下线
    async getAll(limit) {
      const { scoutDogeABI } = this.$wallet
      const res = await scoutDogeABI.getSubordinate(0, limit)
      let number
      for (let i = 0; i < res.length; i++) {
        const item = res[i]
        const data = await scoutDogeABI._purchase(item)
        if (!number) {
          number = data.token
        } else {
          number = number.add(data.token)
        }
      }
      this.total = parseInt(utils.formatUnits(number.mul(2).div(100), 9))
      this.personal = parseInt(utils.formatUnits(number, 9))
      this.personalUSDT = Number(utils.formatEther(this.USDT.mul(this.personal))).toFixed(2)
      this.totalUSDT = Number(utils.formatEther(this.USDT.mul(this.total))).toFixed(2)
    },

    // 获取转换美元实例
    async getUSDT() {
      if (!this.USDT) {
        const outs = await this.$wallet.pancakeRouter.getAmountsOut('1000000000', [
          '0x9E551b430B7Ab145C96c6eDe72a34eC2A90d4eca',
          '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c',
          '0x55d398326f99059ff775485246999027b3197955'
        ])
        this.USDT = outs[outs.length - 1]
      }
    },

    // 查询邀请地址收益
    async searchAddressProfit() {
      this.tableList.forEach((item) => {
        this.$wallet.scoutDogeABI
          ._purchase(item.address)
          .then((data) => {
            item.profit = parseInt(utils.formatUnits(data.token.mul(2).div(100), 9))
            item.usdt = Number(utils.formatEther(this.USDT.mul(item.profit))).toFixed(2)
          })
          .catch(() => {
            item.profit = 'error'
            item.usdt = '0.0000'
          })
      })
    },
    toggleTab(index) {
      this.tabIndex = index
    }
  }
}
</script>

